@import "modules/_importer";

%font-medium-normal {
    font-size: $font-size-medium;
    font-weight: $font-weight-normal;
}

%font-large-light-bold {
    font-size: $font-size-large;
    font-weight: $font-weight-light-bold;
}

%font-medium-bold {
    font-size: $font-size-medium;
    font-weight: $font-weight-bold;
}

%tabs-font-style {
    font-family: $cbe-font-family;
    @extend %font-medium-normal;
}

%validation-font-style {
    font-family: $cbe-font-family;
    @extend %font-medium-normal;
}

%editor-header-title,
%push-notification-field {
    font-family: $cbe-font-family;
    @extend %font-large-light-bold;
}

%upload-frame {
    position: relative;
    padding: 30%;
    margin: 0 auto;
    border: 1px solid $cbe-basic-frame-border-color;
    border-radius: 4px;
}
