// font family
$cbe-font-family: cwTeXYen, sourcehansans-tc, , "Noto Sans TC", "Source Sans Pro", "Helvetica Neue", Helvetica,
    Arial, sans-serif;

// font size
$font-size-large: 1.125rem;
$font-size-normal: 1rem;
$font-size-medium: 0.875rem;
$font-size-small: 0.8125rem;
$font-size-smaller: 0.75rem;

// font weight
$font-weight-normal: 400;
$font-weight-light-bold: 500;
$font-weight-bold: 700;

// setting by component
$cbe-basic-card-text-font-size: 0.875rem;
$button-text-font-size: 1rem;
