.bg-contain {
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
}

.frame {
    background-color: $cbe-primary-background-color;
    cursor: pointer;

    .upload-frame {
        @extend %upload-frame;
    }

    .fileNameArea {
        display: flex;
        width: 100%;
        align-items: center;
        justify-content: center;
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;
        cursor: pointer;
    }
}
